<template>
  <div class="container">
    <div id="map_" style="width:100%; height:100%"></div>
    <div class="add-marker">
      <span>lng：</span>
      <input type="text" v-model="inpLng">
      <span>lat：</span>
      <input type="text" v-model="inpLat">
      <button @click="setMarker">定位</button>
    </div>
    <div class="mouse-lnglat">
      zoom: {{map._zoom}} lnglat: {{mouseLng}},{{mouseLat}}
    </div>
  </div>
</template>

<script>
import { LMap, LTileLayer, LMarker, LPopup, LImageOverlay } from "vue2-leaflet";
import 'leaflet.chinatmsproviders'
export default {
  components: {
    LMap,
    LTileLayer
  },
  data() {
    return {
      initCenter: [31.59, 120.29], // 初始中心点
      zoom: 3,
      mapMinZoom: 3,
      mapMaxZoom: 20,
      map: {},
      mouseLng: '',
      mouseLat: '',
      inpLng: '',
      inpLat: ''
    };
  },
 
  mounted() {
    this.initMap();
  },
  methods: {
    setMarker () {
      const lng = this.inpLng * 1, lat = this.inpLat * 1;
      console.log(lng + ',' + lat);
      if (!lng || !lat || isNaN(lng) || isNaN(lat)) {
        alert("经纬度输入错误")
        return false
      } else {
        L.marker([lat, lng]).addTo(this.map);
        this.map.setView([lat, lng])
      }
     
    },
    initMap() {
     
      var options = {
        maxZoom: this.mapMaxZoom,
        minZoom: this.mapMinZoom,
        opacity: 1,
        tms: false
      }
      // 高德地图
      var gaodem = L.tileLayer.chinaProvider('GaoDe.Normal.Map', options);
      // 高德影像
      var gaodeimgm = L.tileLayer.chinaProvider('GaoDe.Satellite.Map', options);
      var gaodeimga = L.tileLayer.chinaProvider('GaoDe.Satellite.Annotion', options);
      // 天地地图
      var tiandim = L.tileLayer.chinaProvider('TianDiTu.Normal.Map', options)
      var tiandia = L.tileLayer.chinaProvider('TianDiTu.Normal.Annotion', options)
      // 天地影像
      var tiandiimgm = L.tileLayer.chinaProvider('TianDiTu.Satellite.Map', options)
      var tiandiimga = L.tileLayer.chinaProvider('TianDiTu.Satellite.Annotion', options)
      // 天地地形
      var tianditm = L.tileLayer.chinaProvider('TianDiTu.Terrain.Map', options)
      var tiandita = L.tileLayer.chinaProvider('TianDiTu.Terrain.Annotion', options)
      // 谷歌地图
      var googlem = L.tileLayer.chinaProvider('Google.Normal.Map', options)
      // 谷歌影像
      var googleimgm = L.tileLayer.chinaProvider('Google.Satellite.Map', options)
      // 谷歌标注
      var googleimga = L.tileLayer.chinaProvider('Google.Satellite.Annotion', options)
      // Geo 地图
      var geom1 = L.tileLayer.chinaProvider('Geoq.Normal.Map', options);
      var geom2 = L.tileLayer.chinaProvider('Geoq.Normal.PurplishBlue', options);
      var geom3 = L.tileLayer.chinaProvider('Geoq.Normal.Gray', options);
      var geom4 = L.tileLayer.chinaProvider('Geoq.Normal.Warm', options);
      var geom5 = L.tileLayer.chinaProvider('Geoq.Theme.Hydro', options);

      // var geoMap = L.layerGroup([geom1, geom2, geom3, geom4, geom5]);
      // OSM 地图
      var OSMNormal = L.tileLayer.chinaProvider('OSM.Normal.Map', options);

      // 百度地图
      var baidum = L.tileLayer.chinaProvider('Baidu.Normal.Map', options);
      var baiduimgm = L.tileLayer.chinaProvider('Baidu.Satellite.Map', options);
      var baiduimga = L.tileLayer.chinaProvider('Baidu.Satellite.Annotion', options);

      var gaodeNormal = L.layerGroup([gaodem]), // 高德地图
          gaodeImage = L.layerGroup([gaodeimgm, gaodeimga]), // 高德影像
          tiandiNormal = L.layerGroup([tiandim, tiandia]), // 天地地图
          tiandiImage = L.layerGroup([tiandiimgm, tiandiimga]), // 天地影像
          tiandiTerrain = L.layerGroup([tianditm, tiandita]), // 天地地形
          googleNormal = L.layerGroup([googlem]), // 谷歌地图
          googleImageMap = L.layerGroup([googleimgm]), // 谷歌影像
          googleImageAnnotion = L.layerGroup([googleimga]) // 谷歌标注

        var baseLayers = {
            "高德地图": gaodeNormal,
            "高德影像": gaodeNormal,
            "天地地图": tiandiNormal,
            "天地影像": tiandiImage,
            "天地地形": tiandiTerrain,
            "谷歌地图": googleNormal,
            "谷歌影像": googleImageMap,
            "谷歌标注": googleImageAnnotion,
            "Geo地图": geom1,
            "Geo午夜蓝": geom2,
            "Geo灰色": geom3,
            "Geo暖色": geom4,
            "Geo水系": geom5,
            "OSM地图": OSMNormal,
            "百度地图": baidum,
            "百度影像": baiduimgm,
            "百度标注": baiduimga,
        }

      this.map = L.map("map_", {
        center: this.initCenter,
        zoom: this.zoom,
        minZoom: this.mapMinZoom,
        maxZoom: this.mapMaxZoom,
        layers: [gaodeNormal],
        zoomControl: false,
        attributionControl: false,
      })

      L.control.layers(baseLayers, null).addTo(this.map);
      L.control.zoom({
          zoomInTitle: '放大',
          zoomOutTitle: '缩小'
      }).addTo(this.map);

      this.map.on('mousemove', this.handleHover)
    },
    handleHover(e) {
      this.mouseLng = e.latlng.lng.toFixed(6)
      this.mouseLat = e.latlng.lat.toFixed(6)
    }
  },
};
</script>

<style scoped>
.container {
  width: 100vw;
  height: 100vh;
}
#map_ {
  width: 100%;
  height: 100%;
}


.mouse-lnglat {
  position: fixed;
  bottom: 0;
  right: 0;
  padding: 4px 12px;
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
  font-size: 12px;
  z-index: 1000;
}

.add-marker {
  position: fixed;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  height: 40px;
  padding: 0 10px;
  display: flex;
  align-items: center;
  z-index: 1000;
  background-color: #fff;
  box-shadow: 0 1px 5px rgba(0,0,0,0.65);
  border-radius: 4px;
  font-size: 13px;
}

.add-marker input {
  margin-right: 10px;
  padding: 1px 4px;
  outline: none;
  border: none;
  background-color: transparent;
  border: 1px solid #eee;
  border-radius: 4px;
}

.add-marker button {
  padding: 3px 9px;
  outline: none;
  border: none;
  background-color: #409eff;
  color: #fff;
  border-radius: 4px;
  font-size: 12px;
  cursor: 1;
}
</style>


